<template>
<div id="loginForm">
  <div class="container0">
    <form @submit.prevent="Login">
    <div class="messageOrerror">
      <Error v-if="error" :error="error"></Error>
    </div>

        <h2><b>Login</b></h2>
        <hr>

        <div class="form-group">
          <label for="email"><b>Email:</b></label>
          <input type="text" class="form-control" v-model="email" placeholder="Enter Email" name="email" id="email" required>
        </div>

        <div class="form-group">
          <label for="password"><b>Password:</b></label>
          <input type="password" class="form-control" v-model="password" placeholder="Enter Password" name="password" id="password" required>
        </div>

        <button class="mybtn" type="submit">Login</button>
        
        <!--<div class="check">
          <input type="checkbox" checked="checked" name="remember">
          <span class="remember"> Remember me </span><br>
        </div>
        -->
        <div class="container">
          <label class="psw">
            <span> Forgot your pasword?<router-link to="forgot"> Click here</router-link> </span>
          </label>
        </div>
        
    </form>
  </div>

  <div class="newUser">
    <p>New here? Register for free now!</p>
    <router-link to="register" class="goRegister">Create an account</router-link>
  </div>
</div>
</template>

<script>
import axios from 'axios';
import Error from './Error';
//import almacen from "../config/almacen";
//import { required, minLength, email } from 'vuelidate/lib/validators';
//import BaseInput from "../components/BaseInput.vue";


export default {
  name: 'loginForm',
  components:{
    Error,
   // BaseInput,
  },
    data(){
      return{
        email: '',
        password: '',
        error: '',
        message:'',
    }
  },
    methods: {
      async Login(){
        try{
            const response = await axios.post('login',{
                email: this.email,
                password: this.password
            })
            
            localStorage.setItem('token', response.data.token);
            this.$store.dispatch('user',response.data.user);

            if (response.data.user.pokemons == null){
                 this.$router.push('starting');
            } else{
                this.$router.push('home');
            }
           
        } catch (e){
          this.error = 'Invalid username or password!'

        }
    },
    
  },
  
}

</script>

